import axios from "axios";

export const C4Actions = {
   FETCH_COLLABORATORS_BEGIN: 'FETCH_COLLABORATORS_BEGIN',
   FETCH_COLLABORATORS_SUCCESS: 'FETCH_COLLABORATORS_SUCCESS',
   FETCH_COLLABORATORS_FAILURE: 'FETCH_COLLABORATORS_FAILURE',
   SET_QUERY_STATEMENT: 'SET_QUERY_STATEMENT',
   SET_URL: 'SET_URL'
}

export const fetch4CMembersBegin = () => ({
   type: C4Actions.FETCH_COLLABORATORS_BEGIN
});

export const fetch4CMembersSuccess = (collaborators) => ({
   type: C4Actions.FETCH_COLLABORATORS_SUCCESS,
   payload: { collaborators }
})

export const fetch4CMembersFailure = (error) => ({
   type: C4Actions.FETCH_COLLABORATORS_FAILURE,
   payload: { error }
})

export const setURL = (URL) => ({
   type: C4Actions.SET_URL,
   payload: { URL }
})

export const setQueryStatement = (query) => ({
   type: C4Actions.SET_QUERY_STATEMENT,
   payload: { query }
})

export const fetch4CMembers = (url) => {
   return dispatch => {
      dispatch(fetch4CMembersBegin());

      axios.get(url)
         .then (res => {dispatch(fetch4CMembersSuccess(res.data));})
         .catch(err => { dispatch(fetch4CMembersFailure(err));});
   }
}